import React from "react";
import { Dropdown, message} from 'antd';


export default function Header() {
  const onClick = ({ key }) => {
    message.info(`Click on item ${key}`);
  };
  const items= [
    {
      label: ' ',
      key: '1',
    },
    {
      label: '',
      key: '2',
    },
    {
      label: '',
      key: '3',
    },
  ];
  return (
    <header>
      <div className="head">
        <div className="place">
          <div className="place_photo"></div>
          <p className="place_text">-</p>
        </div>
        <div className="logo"></div>
        <ul className="nav">
          <li>
            <div className="welcome_photo"></div>
            <p> </p>
          </li>
          <li>
            <div className="category_photo"></div>
            <Dropdown menu={{ items, onClick }}>
              <p className="category_text"></p>
            </Dropdown>
          </li>
        </ul>
      </div>
      <div className="profile_presentation"></div>
      <div className="profile_welcome">
        <h2 className="profile_welcome__title"> </h2>
        <p>     !      ,   ,       .          !</p>
      </div>
    </header>
  );
}

